import Link from 'next/link'
import Image from 'next/image'
import Profile from './Profile'

const Header = () => {

  return (
    <>
    <nav className='bg-white w-full h-16 flex items-center justify-between p-4'>
        <Image src="/Logo.png" alt="logo" width={100} height={100} />
      <ul className='flex items-center justify-center gap-x-8'>
        <li className=' font-bold hover:text-green-700 transition-all duration-300'>
          <Link href="/">Inicio</Link>
        </li>
        <li>
          <Link href="/">Transacciones</Link>
        </li>
        <li>
          <Link href="/">Presupuesto</Link>
        </li>
        <li>
          <Link href="/">Alertas</Link>
        </li>
        <li>
          <Link href="/">Sugerencias</Link>
        </li>
      </ul>
      <Profile />
    </nav>
    </>
  )
}

export default Header